.name {
    animation: text-appear 1.3s cubic-bezier(.51,.92,.24,1.15);
    font-family: 'Montserrat', serif;
    font-weight: bold;
    font-size: 20pt;
    display: flex;
    align-items: center;
}

.shadower {
    background: rgba(0, 0, 0, 1);
    opacity: 0;
    position: absolute;
    top: 0;
    height: inherit;
    left: 0;
    right: 0;
    animation: shadow 1s linear;
}

.logo-dev{
    margin-bottom: 15px;
    height: 60px;
    width: 112px;
}

@-webkit-keyframes text-appear {
    0% {
        transform: scale(1.6) translateY(50px);
    }
    40% {
        transform: scale(1.6) translateY(50px);
    }
    70% {
        transform: scale(1.1) translateY(25px);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes text-appear {
     0% {
         transform: scale(1.6) translateY(50px);
     }
     40% {
         transform: scale(1.6) translateY(50px);
     }
     70% {
         transform: scale(1.2) translateY(15px);
     }
     100% {
         transform: scale(1);
     }
 }

@keyframes shadow {
    0% {
        opacity: 0.8;
    }
    30% {
        opacity: 0.8;
    }
    100% {
        opacity: 0;
    }
}